.tour-cards {
  max-width: 1170px;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  gap: 30px;

  margin: 0 auto;

  &_card {
    height: 531px;
    width: 370px;

    position: relative;

    border-radius: 50px;
    overflow: hidden;

    transition: transform 300ms ease;
    transform: scale(1);

    &::before {
      content: '';
      height: 100%;
      width: 100%;
      background: linear-gradient(360deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.06) 48.44%, rgba(0, 0, 0, 0.5) 100%);

      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
    }

    &_background {
      width: 100%;
      height: 100%;

      position: absolute;
      top: 0;
      left: 0;

      object-fit: cover;
      z-index: 1;
      transition: transform 300ms ease;
    }

    &_content {
      height: 100%;
      width: 270px;
      position: relative;
      z-index: 2;

      display: flex;
      flex-direction: column;
      justify-content: space-between;

      padding: 50px;
      color: var(--white);
    }

    &_info {
      display: flex;
      flex-direction: column;
      row-gap: 19px;

      &_title {
        font-size: 35px;
        font-weight: 500;
      }

      &_description {
        font-size: 17px;
      }

      &_link {
        bottom: 43px;
        left: 30px;
      }
    }

    &:hover {
      cursor: pointer;
      transform: scale(1.05);

      &::before {
        opacity: 1;
      }

      .tour-cards_card_background {
        transform: scale(1.1);
      }

      .button_detailed {
        background: var(--green-400);
      }
    }

    &:active {
      .button_detailed {
        background: var(--orange-200);
      }
    }
  }
}

@media (max-width: 1024px) {
  .tour-cards {
    gap: 18px;

    &_card {
      width: 450px;

      &_content {
        width: 100%;
      }
    }
  }
}

@media (max-width: 475px) {
  .tour-cards {

    &_card {
      height: 318px;
      width: 318px;

      border-radius: 20px;

      &_content {
        padding: 25px;
      }

      &_info {
        row-gap: 13px;
        &_title {
          font-size: 23px;
          letter-spacing: 1px;
        }
        &_description {
          font-size: 17px;
          letter-spacing: -0.7px;
        }
        &_link {
          bottom: 18px;
          left: 4px;
        }
      }
    }
  }

}